import React, { Component } from 'react'
import uuidv4 from 'uuid/v4';
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";
import Draggable from 'react-draggable';
import { Radio, Menu, Dropdown } from 'antd';
import EditingType from '@/constant/EditingType'
import Size from '@/constant/Size';
import './index.less';

export default class ZoomView extends Component {
    constructor(props) {
        super(props);

        this.componentRef = React.createRef();
        this.zoomRef = React.createRef();
        
        this.state = {
            containerHeight: this.props.height - 54,
            containerWidth: this.props.width,
            zoomState: 'ZoomOut',
            zoomLevel: (this.props.height - 54) / Size[this.props.layout].height,
            cursorType: 0,
            tsKey: uuidv4()
        }
    }

    static getDerivedStateFromProps(props, state) {
        let newState = {}
        if (props.height - 54 !== state.containerHeight) {
            newState = {
                containerHeight: props.height - 54,
                zoomLevel: (props.height - 54) / Size[props.layout].height,
            }
        }

        if (props.width !== state.containerWidth) {
            newState['containerWidth'] = props.width;
            newState['zoomLevel'] = (props.height - 54) / Size[props.layout].height;
        }

        return newState;
    }

    componentDidUpdate = () => {
        if(this.props.scale !== this.state.zoomLevel)
            this.props.updateScale(this.state.zoomLevel);
    }

    setScroll = (status) => {
        var setGrabbingVar = setGrabbing(this.container);
        var setGrabVar = setGrab(this.container);
        function setGrabbing(container) {
            container.style.cursor = 'grabbing'
        }
        function setGrab(container) {
            container.style.cursor = 'grab'
        }
        this.container.scrollTop = 0;
        this.container.style.overflowY = status ? 'scroll' : 'hidden';
        this.container.style.cursor = status ? 'grab' : 'default';
        if (status) {
            this.container.addEventListener('mousedown', setGrabbingVar);
            this.container.addEventListener('mouseup', setGrabVar)
        } else {
            this.container.removeEventListener('mousedown', setGrabbingVar);
            this.container.removeEventListener('mouseup', setGrabVar)
        }
    }

    getZoomLevel(width) {
        let level = [0.1, 0.25, 0.50, 0.75, 1.00, 1.25, 1.50, 2.00]
        return <Menu>
            {
                level.map((subMunue, idx) => {
                    return <Menu.Item key={"subMunue" + idx} className='MenuItem' onClick={() => this.changeZoomLevel(subMunue, this.state.containerWidth, this.state.containerHeight, '', width)}>{subMunue * 100 + '%'}</Menu.Item>
                })
            }
        </Menu>

    }

    changeZoomLevel(level, width, containerHeight, why, pWidth=825) {
        if (level === this.state.zoomLevel) return
        if (Math.abs(this.dragFS.state.y) >= this.state.containerHeight) {
            this.dragFS.setState({
                y: 0
            })
        }
        if ((this.state.zoomLevel < this.state.containerWidth / pWidth && level >= this.state.containerWidth / pWidth) || level < this.state.containerWidth / pWidth || Math.abs(this.dragFS.state.x) >= this.state.containerWidth) {
            this.dragFS.setState({
                x: 0
            })
        }

        let x = level >= this.state.containerWidth / pWidth ? this.dragFS.state.x : (width - pWidth * level) / 2
        if (why === 'height' || (why === 'full' && this.state.containerWidth / pWidth > 1)) {
            this.dragFS.props.children.props.children.ref.current.offsetParent.style.transform = "translate(0px, 0px)"
            this.dragFS.setState({
                y: 0,
                x: 0
            })
        } else if (why === 'full' || why === 'width') {
            this.dragFS.props.children.props.children.ref.current.offsetParent.style.transform = "translate(0px, 0px)"
            this.dragFS.setState({
                y: 0,
                x: 0
            })
            x = 0;
        }
        this.zoomRef.props.transform(x, 0, level, 500, 'easeOut');
        setTimeout(() => {
            this.setState({
                zoomLevel: level
            })
        }, 700)

    }

    clickZoomLevel(direction, pWidth) {
        let newZoomLevel = this.state.zoomLevel + direction * 0.1
        this.zoomRef.props.transform((this.state.containerWidth - pWidth * newZoomLevel) / 2, 0, newZoomLevel, 300, 'easeOut');
        setTimeout(() => {
            this.setState({
                zoomLevel: newZoomLevel
            })
        }, 500)
    }

    changePointer(type) {
        // type: 0 pointer
        // tyoe: 1 hand
        var setGrabbingVar = setGrabbing(this.container);
        var setGrabVar = setGrab(this.container);
        function setGrabbing(container) {
            container.style.cursor = 'grabbing'
        }
        function setGrab(container) {
            container.style.cursor = 'grab'
        }
        this.container.style.cursor = type === 1 ? 'grab' : 'default';
        if (type === 0) {
            this.dragFS.props.children.props.children.ref.current.offsetParent.style.transform = "translate(0px, 0px)";
            this.container.addEventListener('mousedown', setGrabbingVar);
            this.container.addEventListener('mouseup', setGrabVar)
        } else {
            this.container.removeEventListener('mousedown', setGrabbingVar);
            this.container.removeEventListener('mouseup', setGrabVar)
        }
        this.setState({
            cursorType: type
        })
        this.props.updateEditingType(EditingType.CHANGE_EDITABLE);
        this.props.updateEditable(type === 0);
    }

    render() {
        let {children, layout} = this.props;
        
        const {width, height} = Size[layout];

        return (
            <div style={{'position': 'relative', 'height': 'calc(100vh - 53px)', flexGrow: 1}}>
                <div className="zoomContainer" ref={el => (this.container = el)} 
                    style={{ height: `calc(100vh - ${85}px)`, 
                    paddingTop: `calc((100vh - ${85+ this.state.containerHeight}px)/2)`, 
                    // paddingBottom: `calc((100vh - ${85+ this.state.containerHeight}px)/2)`,
                    width: `${this.state.containerWidth}px`, 
                    backgroundOpacity:0, margin: '0 auto'}}>
                    <TransformWrapper
                        key={this.state.tsKey}
                        scale={this.state.zoomLevel}
                        positionX={(this.state.containerWidth - width * this.state.zoomLevel) / 2}
                        positionY={0}
                        options={{
                            limitToBounds: false, minScale: (this.state.containerHeight - 100) / height, centerContent: false
                        }}
                        pan={{
                            disabled: true,
                            lockAxisX: true,
                            lockAxisY: false,
                            velocityEqualToMove: true,
                            enableVelocity: true,
                            wheelEnabled: true
                        }}
                        wheel={{
                            wheelEnabled: false,
                            touchPadEnabled: false,
                            step:0
                        }}
                        doubleClick={{ disabled: true }}
                    >
                        {({ setTransform, resetTransform, setDefaultState }) => (
                            <React.Fragment >
                                <div className="tools" style={{ display: 'none' }}>
                                    <Radio ref={el => this.zoomRef = el} transform={setTransform} reset={resetTransform} re={setDefaultState} ></Radio>
                                </div>
                                <TransformComponent style={{ margin: '0 auto' }}>
                                    <Draggable defaultPosition={{ x: 0, y: 0 }} axis={this.state.zoomLevel >= this.state.containerWidth / width ? 'both' : 'y'} cancel=".fs_edit" disabled={this.state.cursorType === 0 ? true : false} enableUserSelectHack={true} ref={el => this.dragFS = el}>
                                        <div style={{ width: `${this.state.containerWidth}px`, height: `${this.state.containerWidth / width * height}px`, minHeight: `${this.props.forPublish ? '' : this.state.containerHeight + 'px'}` }}>
                                            <div ref={this.componentRef}>
                                            {
                                                children
                                            }
                                            </div>
                                        </div>
                                    </Draggable>
                                </TransformComponent>
                            </React.Fragment>
                        )}
                    </TransformWrapper>
                </div>
                <div className="bottomTools" >
                    <div className={"toolBtn toolFull"} onClick={() => this.changeZoomLevel(1, this.state.containerWidth, this.state.containerHeight, 'full', width)}></div>
                    <div className={"toolBtn toolWidth"} onClick={() => this.changeZoomLevel(this.state.containerWidth / width, this.state.containerWidth, this.state.containerHeight, 'width', width)}></div>
                    <div className={"toolBtn toolHeight"} onClick={() => this.changeZoomLevel(this.state.containerHeight / height, this.state.containerWidth, this.state.containerHeight, 'height', width)}></div>
                    <div className="fs-split">
                        <i aria-label="icon: plus" className="anticon anticon-plus"><svg style={{ color: '#bec1c3' }} viewBox="64 64 896 896" focusable="false" className="" data-icon="plus" width="1em" height="1em" fill="currentColor" aria-hidden="true"><path d="M482 152h60q8 0 8 8v704q0 8-8 8h-60q-8 0-8-8V160q0-8 8-8z"></path></svg></i>
                    </div>
                    <div className="fs-zoom">
                        <Dropdown overlay={this.getZoomLevel(width)} trigger={['click']} style={{ color: '#bec1c3' }}>
                            <div className="ant-dropdown-link" onClick={e => e.preventDefault()} style={{ marginBottom: '1px' }}>
                                <span className="fs-zoom-text" style={{ width: "35px" }}>{Math.round(this.state.zoomLevel * 100) + '%'}</span>
                                <div className={"toolBtn toolSort"} style={{ display: 'inline-block', width: '7px', paddingLeft: '2px' }}></div>
                            </div>
                        </Dropdown>
                    </div>

                    <div className={"toolBtn toolZoomMin"} onClick={() => this.clickZoomLevel(-1, width)}></div>
                    <div className={"toolBtn toolZoomAdd"} onClick={() => this.clickZoomLevel(1, width)}></div>
                    <div className="fs-split" >
                        <i aria-label="icon: plus" className="anticon anticon-plus"><svg style={{ color: '#bec1c3' }} viewBox="64 64 896 896" focusable="false" className="" data-icon="plus" width="1em" height="1em" fill="currentColor" aria-hidden="true"><path d="M482 152h60q8 0 8 8v704q0 8-8 8h-60q-8 0-8-8V160q0-8 8-8z"></path></svg></i>
                    </div>
                    <div className={this.state.cursorType === 1 ? "toolBtn toolHandChecked" : "toolBtn toolHand"} onClick={() => this.changePointer(1)}></div>
                    <div className={this.state.cursorType === 0 ? "toolBtn toolPointerChecked" : "toolBtn toolPointer"} onClick={() => this.changePointer(0)}></div>
                </div>
            </div>
        )
    }
}
